/* eslint-disable no-template-curly-in-string */
import React, { useState } from "react";
import { render as renderAmis } from "amis";
import { Editor } from "amis-editor";

const Home = () => {
  const [preview, setPreview] = useState(false);
  const [editorValue, setEditorValue] = useState(undefined);

  return (
    <div style={{ width: "100%", height: "100%" }}>
      {renderAmis({
        type: "page",
        body: [
          {
            type: "page",
            body: {
              type: "form",
              api: {
                method: "post",
                url: "/api/test/saveForm",
                data: {
                  name: "${name}",
                  email: "${email}",
                  editorValue,
                },
              },

              controls: [
                {
                  type: "text",
                  name: "name",
                  label: "姓名：",
                },
                {
                  name: "email",
                  type: "email",
                  label: "邮箱：",
                },
              ],
            },
          },
          {
            type: "page",
            body: {
              label: preview ? "关闭" : "预览",
              type: "action",
              onClick: () => {
                setPreview(!preview);
              },
            },
          },
        ],
      })}

      <Editor
        value={editorValue}
        onChange={(value: any) => {
          setEditorValue(value);
          console.log(value);
          console.log(JSON.stringify(value));
        }}
        preview={preview}
      />
    </div>
  );
};

export default Home;
